import axios from "axios";
import { createContext } from "react";

export const WeatherContext = createContext(null)
export function WeatherContextProvider({children})
{

async function getData(country='london')
{
  let {data}=  await axios.get(`https://api.weatherapi.com/v1/forecast.json?key=c5e3af3bed8d47699c6200421200609&q=${country}&aqi=yes&days=5`)
  return data
}

return <WeatherContext.Provider value={{getData}}>
   {children}
</WeatherContext.Provider>

}