
import React, { useContext, useEffect, useState } from 'react'
import { WeatherContext } from '../../WeatherContext/WeatherContext'
import { Autocomplete, Card, TextField } from '@mui/material'
import Skelton from '../Skelton'
import img from '../../../src/images/6707.jpg'
import img1 from '../../../src/images/1.jpg'
import img2 from '../../../src/images/3.avif'

export default function Weather() {
  let { getData } = useContext(WeatherContext)

  let [forecast, setForecatday] = useState([])
  let [loc, setLoc] = useState('')
  let imgs = [img, img1, img2]
  let [globalIndex, setglobalIndex] = useState(0);
  async function getDataFromInput(e) {

    let term = e.target.innerHTML
    let data = await getData(term)
    setForecatday(data.forecast.forecastday);
    setLoc(data.location.name);
    let index = Math.floor(Math.random() * imgs.length)
    setglobalIndex(index)
  }


  async function getDataFun() {
    let data = await getData()
    setForecatday(data.forecast.forecastday);
    setLoc(data.location.name);
  }
 
  useEffect(() => {
    getDataFun()
   
  }, [])
  const counties = ['london', 'cairo','paris','canada','france','australia'];
  return (
    <div className='container'>

      <div className="searchBar my-4">
        <Autocomplete
          className='shadow-lg my-5 text-white'
          onChange={getDataFromInput}
          disablePortal
          id="combo-box-demo"
          options={counties}
          sx={{ width: "50%" }}
          renderInput={(params) => <TextField {...params} label="Country" />}
        />
      </div>
      <div className="row my-5 align-items-stretch">

        {forecast.length > 0 ?
          forecast.map((day, index) =>
            <div className="col-md-4 " >
              <Card variant="outlined" style={index == 0 ? { backgroundImage: `url(${imgs[globalIndex]})`, backgroundSize: 'cover', backgroundPosition: 'center' } : {}} className='p-3 h-100 bg-transparent shadow' >
                {index == 0 ? <p>{loc}</p> : ''}
                <div className="date-box d-flex justify-content-between align-items-center">
                  <h4>{new Date(day.date).toLocaleString('en-su', { dateStyle: 'medium', hour12: false })}</h4>
                  <p style={{ color: '#F2D680', fontWeight: 'bolder' }}>{new Date(day.date).toLocaleString('en-su', { weekday: 'long' })}</p>
                </div>
                {index == 0 ? <h2>{day.day.avgtemp_c} C</h2> : <h4>{day.day.avgtemp_c} C</h4>}
                <p className='my-3 text-muted '>{day.day.condition.text}</p>
                <img src={`http:${day.day.condition.icon}`} alt="" />
              </Card>
            </div>)
          : <Skelton></Skelton>}

      </div>
    </div>
  )
}
